
import mockData from "./mockData";

export default {
    state: {
        info: {}
    },
    mutations: {
        setInfo(state, info) {
            state.info = info
        },
        clearInfo(state) {
            state.info = {}
        }
    },
    actions: {
        async fetchInfo({commit}) {
            try {
                const resp = mockData.info
                commit('setInfo', resp.data)
            } catch (e) {
                const eDescription = 'Ошибка запроса данных пользователя!'
                commit('setError', eDescription)
                throw eDescription
            }
        }
    },
    getters: {
        info: s => s.info
    }
}
