import mockData from './mockData'

export default {
    actions: {
        async fetchEmployeeData({commit}) {
            try {
                const resp = mockData.data
                return resp
            } catch (e) {
                console.log(e)
                const eDescription = 'Ошибка запроса данных работника!'
                commit('setError', eDescription)
                throw eDescription
            }
        },
        async fetchEmployeePaymentList({commit}) {
            try {
                const resp = mockData.paymentList
                return resp
            } catch (e) {
                console.log(e)
                const eDescription = 'Ошибка запроса данных расчетного листка!'
                commit('setError', eDescription)
                throw eDescription
            }
        },
        async fetchEmployeeWorkScheduleData({commit}) {
            try {
                const resp = mockData.employeeWorkSchedulesDataForPeriod
                return resp
            } catch (e) {
                console.log(e)
                const eDescription = 'Ошибка запроса данных графика работы!'
                commit('setError', eDescription)
                throw eDescription
            }
        },
        async fetchEmployeeTabel({commit}) {
            try {
                const resp = mockData.tabel
                for (let itemData of resp){
                    itemData.tabel_data = JSON.parse(itemData.tabel_data)
                }

                return resp
            } catch (e) {
                console.log(e)
                const eDescription = 'Ошибка запроса данных табеля работы!'
                commit('setError', eDescription)
                throw eDescription
            }
        }
    }
}
